var _self;
export default{
	
	data() {
		return {
			loginInfo:{},
			isFirst:false,
			orderInfo:{}
		}
	},
	onLoad(opt) {
		_self = this;
		var loginRes = this.checkLogin('../index/recommend', '2');
		if(!loginRes){return false;}
		this.loginInfo = loginRes;
		
		if(opt.first != undefined){
			_self.isFirst = true;
		}
		
		this.getOrderInfo();

	},
	onShow() {
		
	},
	components:{
		
	},
	methods:{
		//返回推荐页
		backTuijian(){
			uni.switchTab({
				url:"../index/recommend"
			})
		},
		//订单信息
		getOrderInfo(){
			
			uni.request({
			    url: _self.$httpUrl+'index.php/api/recommend/getUserorder', 
				method:"POST",
				data:{
					user_id:_self.loginInfo.uid
				},
			    success: (res) => {
			     
			      console.log(res);
				  
				  if(res.data.status == 1){
					_self.orderInfo = res.data.result;
			
				  }else{
					  uni.showToast({
					  	icon:"none",
					  	title:res.data.msg
					  })
				  }
				  
			    },fail: (e) => {
					
			    },complete: (e) => {
					
			    }
			});
			
			
			
			
		},
		backBeforePage:()=>{
			
			uni.navigateBack({
				delta:1
			})
		}
		
		
	}
	
}

